.person {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    margin-bottom: 28px;

    @include onDesktop {
      margin-bottom: 32px;
    }
  }

  &__photo {
    grid-column: 2 / 4;
    width: 100%;

    border-radius: 50%;
  }

  &__name {
    margin: 0 0 4px;

    font-weight: bold;
    font-size: 24px;
    line-height: 30px;

    text-align: center;

    color: $colorBlue;
  }

  &__position {
    margin: 0 0 4px;

    font-weight: normal;
    font-size: 14px;
    line-height: 27px;

    text-align: center;

    color: #9fadc2;
  }

  &__description {
    margin: 0;

    text-align: center;
  }
}
